  }
}
@media (max-width: 1056px) {
  .feedback_card {
    width: 450px;
  }
}
@media (max-width: 960px) {
  .feedback_card {
    width: 400px;
  }
}
@media (max-width: 860px) {
  .feedback_card {
    width: 350px;
  }
}
@media (max-width: 760px) {
